import { useState, useRef, useEffect } from "react";

const useAudio = (audioUrl) => {
  const [isAudioEnabled, setIsAudioEnabled] = useState(false);
  const audioRef = useRef(null);
  const synthRef = useRef(window.speechSynthesis);
  const utteranceRef = useRef(null);

  const isSpeechSynthesisSupported = () => "speechSynthesis" in window;

  const toggleAudio = () => {
    console.log("Toggle audio called. Audio URL:", audioUrl); // Log para verificar o URL do udio
    console.log("Current audio state:", isAudioEnabled); // Log para verificar o estado atual do udio

    if (audioUrl) {
      if (!audioRef.current) {
        console.log("Creating new Audio instance with URL:", audioUrl); // Log para verificar a criao do udio
        audioRef.current = new Audio(audioUrl);

        // Adiciona listeners para depurao
        audioRef.current.addEventListener("play", () => {
          console.log("udio comeou a tocar.");
        });
        audioRef.current.addEventListener("pause", () => {
          console.log("udio pausado.");
        });
        audioRef.current.addEventListener("ended", () => {
          console.log("udio terminou.");
        });
        audioRef.current.addEventListener("error", (e) => {
          console.error("Erro ao reproduzir udio:", e);
        });
      }

      if (isAudioEnabled) {
        console.log("Pausing audio..."); // Log para verificar a pausa do udio
        audioRef.current.pause();
      } else {
        console.log("Playing audio..."); // Log para verificar a reproduo do udio
        audioRef.current.play().catch((error) => {
          console.error("Erro ao reproduzir udio:", error); // Log de erro caso a reproduo falhe
          alert("Erro ao reproduzir udio. Clique novamente para tentar.");
        });
      }
    } else if (isSpeechSynthesisSupported()) {
      if (isAudioEnabled) {
        console.log("Canceling speech synthesis..."); // Log para verificar o cancelamento da sntese de fala
        synthRef.current.cancel();
      } else {
        const content = "Texto de exemplo para sntese de fala."; // Substitua pelo contedo real
        console.log("Starting speech synthesis with content:", content); // Log para verificar o contedo da sntese de fala
        utteranceRef.current = new SpeechSynthesisUtterance(content);
        utteranceRef.current.lang = "pt-BR";
        synthRef.current.speak(utteranceRef.current);
      }
    } else {
      console.warn("Navegador no suporta funcionalidade de udio."); // Log para navegadores sem suporte
      alert("Seu navegador no suporta a funcionalidade de udio.");
    }
    setIsAudioEnabled(!isAudioEnabled);
  };

  useEffect(() => {
    // Soluo para o warning do ESLint
    const synth = synthRef.current;

    return () => {
      console.log("Cleaning up audio and speech synthesis..."); // Log para verificar a limpeza
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current = null;
      }
      if (synth) {
        synth.cancel();
      }
    };
  }, []);

  return { isAudioEnabled, toggleAudio };
};

export default useAudio;
